import React from 'react';
import { useState } from 'react';
import ".././App.css";
import ReactSimplyCarousel from 'react-simply-carousel';
import image1 from './images/carousel1.jpg';
import image2 from './images/carousel2.jpg';
import image3 from './images/carousel3.jpg';



const Carousel = () => {
  const [activeSlideIndex, setActiveSlideIndex] = useState(0);

  return (
    <div>
      <ReactSimplyCarousel
        activeSlideIndex={activeSlideIndex}
        onRequestChange={setActiveSlideIndex}
        itemsToShow={1}
        itemsToScroll={1}
        forwardBtnProps={{
         
          style: {
            alignSelf: 'center',
            background: 'black',
            border: 'none',
            borderRadius: '50%',
            color: 'white',
            cursor: 'pointer',
            fontSize: '20px',
            height: 30,
            lineHeight: 1,
            textAlign: 'center',
            width: 30,
          },
          children: <span>{`>`}</span>,
        }}
        backwardBtnProps={{
          
          style: {
            alignSelf: 'center',
            background: 'black',
            border: 'none',
            borderRadius: '50%',
            color: 'white',
            cursor: 'pointer',
            fontSize: '20px',
            height: 30,
            lineHeight: 1,
            textAlign: 'center',
            width: 30,
          },
          children: <span>{`<`}</span>,
        }}
        responsiveProps={[
          {
            itemsToShow: 1,
            itemsToScroll: 1,
            minWidth: 768,
          },
        ]}
        speed={100}
        easing="linear"
      >
        
        <img className="carouselImage" src={image1} />
        <img className="carouselImage" src={image2}/>
        <img className="carouselImage" src={image3}/>
        
      </ReactSimplyCarousel>
    </div>
  );
}

export default Carousel;